import { useState } from "react";
import Button from "react-bootstrap/Button";
import { Form, ProgressBar } from "react-bootstrap";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay, faStop } from "@fortawesome/free-solid-svg-icons";
import * as Tone from "tone";

var playingFrequencies = [];

function setFrequencies(frequencies, volume) {
  frequencies.forEach((frequency) => {
    console.log("-->", frequency.value);
    let osc = getNewOscillator();
    playingFrequencies.push(osc);
    osc.set({ frequency: frequency.value, volume: volume });
    osc.start();
  });
}

function stopAllFrequencies() {
  playingFrequencies.forEach((osc) => {
    osc.stop();
  });
}

function getNewOscillator(volume) {
  return new Tone.Oscillator({
    type: "square",
    frequency: 440,
    volume: volume,
  }).toDestination();
}

const Player = (props) => {
  const [isPlaying, setIsPlaying] = useState(false);

  function handleChange(propName, value) {
    console.log(propName, value);
    handleValueChange(propName, value);
    props.onChange(propName, value);
  }

  function handleValueChange(propName, value) {
    if (propName === "volume") {
      playingFrequencies.forEach((osc) => {
        osc.set({ volume: value });
      });
    }
  }

  function playClick() {
    props.state.frequencies = [];
    //move programFrequencies in to frequencies
    props.state.programFrequencies.map((freq) => {
      let isValidFrequency = validateFrequency(freq.value);
      if (isValidFrequency) {
        props.state.frequencies.push({ value: freq.value });
      }
    });

    if (props.state.frequencies.length === 0) {
      return;
    }

    let isPlayingNow = !isPlaying;
    setIsPlaying(isPlayingNow);
    console.log("playClick", props.state);

    if (isPlayingNow) {
      console.log("playing", props.state.frequencies);
      if (props.state.frequencies) {
        setFrequencies(props.state.frequencies, props.state.volume);
      }
    } else {
      console.log("stop");
      stopAllFrequencies(props.state.frequencies);
    }
  }

  function validateFrequency(input) {
    let isNumber = /^\d+$/.test(input);
    if (!isNumber) {
      return undefined;
    }

    let freq = input % 1 === 0;

    if (freq) {
      if (input > 10 && input < 15001) {
        return freq;
      } else {
        console.log("skipping: " + input);
      }
    }
  }

  const PlayButton = () => (
    <Button variant="outline-success" onClick={playClick}>
      <FontAwesomeIcon icon={faPlay} /> Play
    </Button>
  );

  const StopButton = () => (
    <>
      <Button variant="outline-danger" onClick={playClick}>
        <FontAwesomeIcon icon={faStop} /> Stop
      </Button>
      <ProgressBar animated striped variant="success" now={45} />
    </>
  );

  return (
    <Form>
      <Form.Group controlId="formBasicRange">
        <div className="d-none">
          <Form.Label>Time (Minutes)</Form.Label>
          <Form.Control
            type="text"
            onChange={(event) => handleChange("time", event.target.value)}
          />

          <Form.Label>Waveform Type</Form.Label>
          <div key={`inline-radio-waveformType`} className="mb-3">
            <Form.Check
              inline
              type="radio"
              label="Sine"
              name="waveformType"
              id="sine"
              onClick={(event) => handleChange("Mode", event.target.id)}
            />
            <Form.Check
              inline
              type="radio"
              label="Square"
              name="waveformType"
              id="square"
              onClick={(event) => handleChange("playMode", event.target.id)}
            />
          </div>

          <Form.Label>Play Mode</Form.Label>
          <div key={`inline-radio-playMode`} className="mb-3">
            <Form.Check
              inline
              type="radio"
              label="Serial"
              name="playMode"
              id="serial"
              onClick={(event) => handleChange("playMode", event.target.id)}
            />
            <Form.Check
              inline
              type="radio"
              label="Parallel"
              name="playMode"
              id="parallel"
              onClick={(event) => handleChange("playMode", event.target.id)}
            />
          </div>
        </div>
        <Form.Label>Volume</Form.Label>
        <Form.Control
          type="range"
          min="-100"
          max="0"
          defaultValue="-50"
          onChange={(event) => handleChange("volume", event.target.value)}
        />

        {isPlaying === false ? <PlayButton /> : null}
        {isPlaying ? <StopButton /> : null}
      </Form.Group>
    </Form>
  );
};

export default Player;
