import React from "react";
// import ReactDOM from "react-dom";
import Form from "react-bootstrap/Form";
import { frequencies } from "../data/rife-data";
const frequenciesForSelect = frequencies.map((data, key) => {
  return { key: key, title: data.title };
});

const ProgramList = (props) => {
  const [searchTerm, setSearchTerm] = React.useState("");
  const [searchResults, setSearchResults] = React.useState([]);
  const handleChange = (event) => {
    setSearchTerm(event.target.value.toLowerCase());
  };

  const selectProgram = (event) => {
    let programTitle = event.target.text;
    const program = frequencies.filter(
      (frequency) => frequency.title === programTitle
    );
    props.onChange("program", programTitle);
    let freqList = program[0].frequencies.map((f) =>{
      return { label: f, value: f };
    });

    props.onChange("programFrequencies", freqList);

    // setSearchTerm(event.target.value.toLowerCase());
  };

  React.useEffect(() => {
    if (searchTerm.length > 1) {
      const results = frequenciesForSelect.filter((frequency) =>
        frequency.title.toLowerCase().includes(searchTerm)
      );
      setSearchResults(results);
    } else {
      setSearchResults(frequenciesForSelect.slice(0,500));
    }
  }, [searchTerm]);

  return (
    <div className="search">
      <Form.Control
        type="text"
        placeholder="Search"
        value={searchTerm}
        onChange={handleChange}
        className="search-input"
      />

      <Form.Control
        as="select"
        className="program-list"
        name="programs"
        id="programs"
        multiple
        onClick={selectProgram}
      >
        {searchResults.map((data, key) => {
          return <option value={key}>{data.title}</option>;
        })}
      </Form.Control>
    </div>
  );
};

export default ProgramList;
